body {
  --theme-text-color: #111321;
  --theme-bg-color: #f4f4f4;
  --theme-switch-light-side: #4b48db;
  --theme-switch-dark-side: #5f6775;
  --theme-tgl-bg: #4b48db;
}

body.dark-theme {
  --theme-text-color: #f4f4f4;
  --theme-bg-color: #111321;
  --theme-switch-light-side: #5f6775;
  --theme-switch-dark-side: #4b48db;
  --theme-tgl-bg: #fff;
}

:root {
  --font-main: "Manrope", sans-serif;
  --font-second: "Poppins", sans-serif;
  --font-weather: "Roboto", sans-serif;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-menu: 750ms cubic-bezier(.48, 1.75, .55, .91);
  --brand-main: #4440f6;
  --brand-main-opt: #4b48db;
  --brand-green: #00dd73;
  --brand-grey: #a8a8a8;
  --brand-grey-dark: #2e2e2e;
  --brand-light: #fff;
  --brand-black: #111321;
  --bg-menu: #f8f8f8;
  --button-color: #a2a2a2;
  --icon-stroke-color: #f4f4f4;
  --main-transparent-color: rgba(68, 64, 247, .7);
  --read-cover-color: rgba(244, 244, 244, .4);
  --footer: #7a7a7a;
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

