.switch-input {
  height: 0;
  width: 0;
  visibility: hidden;
}

.switch-input:checked + label {
  background: var(--brand-main-opt);
}

.switch-input:checked + .switch-lbl:after {
  translate: -100%;
  transform: translate --transition;
  left: calc(100% - 1px);
}

.switch-lbl {
  cursor: pointer;
  width: 40px;
  height: 20px;
  background-color: var(--brand-light);
  border: 1px solid var(--theme-tgl-bg);
  border-radius: 20px;
  margin-right: 12px;
  display: block;
  position: relative;
}

.switch-lbl:after {
  content: "";
  width: 16px;
  height: 16px;
  background-color: var(--theme-tgl-bg);
  border-radius: 20px;
  transition: all .3s;
  position: absolute;
  top: 50%;
